@font-face {
  font-family: "MontSerrat";
  src: local("MontSerrat-Regular"), local("MontSerrat-Regular"), url("../fonts/MontSerrat-Regular.woff2") format("woff2"), url("../fonts/MontSerrat-Regular.woff") format("woff");
  font-weight: 400;
}
@font-face {
  font-family: "MontSerrat";
  src: local("MontSerrat-Medium"), local("MontSerrat-Medium"), url("../fonts/MontSerrat-Medium.woff2") format("woff2"), url("../fonts/MontSerrat-Medium.woff") format("woff");
  font-weight: 500;
}
@font-face {
  font-family: "MontSerrat";
  src: local("MontSerrat-SemiBold"), local("MontSerrat-SemiBold"), url("../fonts/MontSerrat-SemiBold.woff2") format("woff2"), url("../fonts/MontSerrat-SemiBold.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "MontSerrat";
  src: local("MontSerrat-Bold"), local("MontSerrat-Bold"), url("../fonts/MontSerrat-Bold.woff2") format("woff2"), url("../fonts/MontSerrat-Bold.woff") format("woff");
  font-weight: 700;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: "Montserrat";
  background-color: #f9fbfd;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.input-message {
  color: #f9fbfd;
}

.overflow-hidden {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  min-height: 100vh;
  width: 100%;
  position: relative;
}

.header-image {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header-about {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header-about h1 {
  font-weight: 700;
  font-size: 54px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  line-height: 1.1;
}
@media (max-width: 780px) {
  .header-about h1 {
    font-size: 36px;
    margin-bottom: 10px;
  }
}

.header-about p {
  text-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  font-weight: 400;
  font-size: 20px;
  color: #f9fbfd;
}
@media (max-width: 780px) {
  .header-about p {
    font-size: 16px;
  }
}

.nav {
  z-index: 1;
  position: relative;
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 30px 100px;
}
@media (max-width: 1080px) {
  .nav {
    padding: 20px 40px;
  }
}
@media (max-width: 780px) {
  .nav {
    padding: 20px;
  }
}
.nav ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}
@media (max-width: 780px) {
  .nav ul {
    display: none;
  }
}
.nav ul li {
  list-style: none;
}
.nav ul a {
  font-weight: 500;
  font-size: 16px;
  color: #f9fbfd;
  transition-duration: 0.5s;
  transition-property: opacity;
}
.nav ul a:hover {
  opacity: 0.6;
}

.nav a {
  transition-duration: 0.5s;
  transition-property: opacity;
}
.nav a:hover {
  opacity: 0.6;
}